import React from 'react';
import PrivacyPolicySections from '../privacy_policy/PrivacyPolicySections';
import '../PrivacyPolicy.css';

function PrivacyPolicy() {
	return (
		<div className="privacy_policy">
			<div className="privacy_header">
				<h1>Privacy Policy</h1>
			</div>
			<div className="basmalah">
				<h3>Bismillāhir-Raḥmānir-Raḥīm (بِسْمِ ٱللَّٰهِ ٱلرَّحْمَٰنِ ٱلرَّحِيمِ)</h3>
			</div>
			<PrivacyPolicySections
				header="PRIVACY NOTICE"
				subheader="Last updated July 27, 2020"
				notes={privacyNotice}
			/>
			<PrivacyPolicySections
				header="1. WHAT INFORMATION DO WE COLLECT?"
				subheader="We only collect information regarding your authentication, identity, mobile device's camera and storage"
				notes={informationWeCollect}
			/>
			<PrivacyPolicySections
				header="2. WILL YOUR INFORMATION BE SHARED WITH ANYONE?"
				subheader="Your information will not be shared with anyone"
				notes={informationShared}
			/>
			<PrivacyPolicySections
				header="3. IS YOUR INFORMATION TRANSFERRED INTERNATIONALLY?"
				subheader="We may transfer, store, and process your information in countries other than your own."
				notes={informationTransferred}
			/>
			<PrivacyPolicySections
				header="4. HOW LONG DO WE KEEP YOUR INFORMATION?"
				subheader="We keep your information for as long as necessary to fulfill the purposes outlined in this privacy notice unless otherwise required by law."
				notes={howLongKeepInfo}
			/>
			<PrivacyPolicySections
				header="5. HOW DO WE KEEP YOUR INFORMATION SAFE?"
				subheader="We aim to protect your personal information through a system of organizational and technical security measures."
				notes={howKeepInfoSafe}
			/>
			<PrivacyPolicySections
				header="6. DO WE COLLECT INFORMATION FROM MINORS?"
				subheader="We do not knowingly collect data from or market to children under 18 years of age."
				notes={infoFromMinors}
			/>
			<PrivacyPolicySections
				header="7. WHAT ARE YOUR PRIVACY RIGHTS?"
				subheader="You may review, change, or terminate your account at any time."
				notes={privacyRights}
			/>
			<PrivacyPolicySections header="8. CONTROLS FOR DO-NOT-TRACK FEATURES" subheader="" notes={privacyNotice} />
			<PrivacyPolicySections
				header="9. DO CALIFORNIA RESIDENTS HAVE SPECIFIC PRIVACY RIGHTS?"
				subheader="Yes, if you are a resident of California, you are granted specific rights regarding access to your personal information."
				notes={doNotTrack}
			/>
			<PrivacyPolicySections
				header="10. DO WE MAKE UPDATES TO THIS NOTICE?"
				subheader="Yes, we will update this notice as necessary to stay compliant with relevant laws."
				notes={caliRights}
			/>
			<PrivacyPolicySections
				header="11. HOW CAN YOU CONTACT US ABOUT THIS NOTICE?"
				subheader="If you have questions or comments about this notice, you may email us at admin@deenify.com or go to the ' Report a problem screen'"
				notes={noticeUpdate}
			/>
			<PrivacyPolicySections
				header="12. HOW CAN YOU REVIEW, UPDATE, OR DELETE THE DATA WE COLLECT FROM YOU?"
				subheader="You have the right to request access to the personal information we collect from you, change that information, or delete it."
				notes={updateData}
			/>
		</div>
	);
}

let privacyNotice =
	'Thank you for choosing to be part of our community at Deenify (“Company”, “we”, “us”, or “our”). We are committed to protecting your personal information and your right to privacy. If you have any questions or concerns about this privacy notice, or our practices with regards to your personal information, please contact us at admin@deenify.io\n.When you use our mobile application, as the case may be (the "App") and more generally, use any of our services (the "Services", which include the App), we appreciate that you are trusting us with your personal information. We take your privacy very seriously. In this privacy notice, we seek to explain to you in the clearest way possible what information we collect, how we use it and what rights you have in relation to it. We hope you take some time to read through it carefully, as it is important. If there are any terms in this privacy notice that you do not agree with, please discontinue use of our Services immediately. This privacy notice applies to all information collected through our Services (which, as described above), as well as any related services, sales, marketing or events. Please read this privacy notice carefully as it will help you understand what we do with the information that we collect.';

let informationWeCollect =
	'If you use our App, we also collect your email and password for authentication purposes, your name, bio and profile picture for identification and the information that we may use "to request access or permission to your mobile device’s" camera and storage in order to create a media post or make changes to your profile. We only collect this information when you try to use these features. If you wish to change our access or permissions, " you may do so in your device’s settings.The information is primarily needed to maintain " the security, user friendliness and operation of our App';

let informationShared =
	'Note: Your profile, posts and comments on posts are public and can be seen by anyone with a Deenify account. We will not share other info like your email and user ID with anyone nor we will give access to your account to anyone unless required. Consent: We may share your info if you have given us specific consent to use your personal information in a specific purpose.Performance of a Contract: Where we have entered into a contract with you, we may process your personal information to fulfill the terms of our contract. Legal Obligations: We only disclose your information where we are legally required to do so in order to comply with applicable law governmental requests, a judicial proceeding, court order, or legal process, such as in response to a court order or a subpoena including in response to public authorities to meet national security or law enforcement requirements). Vital Interests: We may disclose your information where we believe it is necessary to investigate, prevent, or take action regarding potential violations of our policies, suspected fraud, situations involving potential threats to the safety of any person and illegal activities, or as evidence in litigation in which we are involved More specifically, we may need to process your data or share your personal information in the following situations: Business Transfers. We may share or transfer your information in connection with, or during negotiations of, any merger, sale of company assets, financing, or acquisition of all or a portion of our business to another company.';

let informationTransferred =
	'Our servers are located in google cloud platform around the globe. If you are accessing our App from outside USA, please be aware that your information may be transferred to, stored, and processed by us in our servers in the google cloud platform with whom we may share your personal information (see "WILL YOUR INFORMATION BE SHARED WITH ANYONE?" above), in and other countries.If you are a resident in the European Economic Area, then these countries may not necessarily have data protection laws or other similar laws as comprehensive as those in your country. We will however take all necessary measures to protect your personal information in accordance with this privacy notice and applicable law.';

let howLongKeepInfo =
	'We will only keep your personal information for as long as it is necessary for the purposes set out in this privacy notice, unless a longer retention period is required or permitted by law (such as tax, accounting or other legal requirements). No purpose in this notice will require us keeping your personal information for longer than the period of time in which users have an account with us.When we have no ongoing legitimate business need to process your personal information, we will either delete or anonymize such information, or, if this is not possible (for example, because your personal information has been stored in backup archives), then we will securely store your personal information and isolate it from any further processing until deletion is possible.';

let howKeepInfoSafe =
	'We have implemented appropriate technical and organizational security measures designed to protect the security of any personal information we process. However, despite our safeguards and efforts to secure your information, no electronic transmission over the Internet or information storage technology can be guaranteed to be 100% secure, so we cannot promise or guarantee that hackers, cybercriminals, or other unauthorized third parties will not be able to defeat our security, and improperly collect, access, steal, or modify your information. Although we will do our best to protect your personal information, transmission of personal information to and from our App is at your own risk. You should only access the App within a secure environment.';

let infoFromMinors =
	'We do not knowingly solicit data from or market to children under 18 years of age. By using the App, you represent that you are at least 18 or that you are the parent or guardian of such a minor and consent to such minor dependent’s use of the App. If we learn that personal information from users less than 18 years of age has been collected, we will deactivate the account and take reasonable measures to promptly delete such data from our records. If you become aware of any data we may have collected from children under age 18, please contact us at admin@deenify.io.';

let privacyRights =
	'If you are resident in the European Economic Area and you believe we are unlawfully processing your personal information, you also have the right to complain to your local data protection supervisory authority. You can find their contact details here: http://ec.europa.eu/justice/data-protect ion/bodies/authorities/index_en.htm. If you are resident in Switzerland, the contact details for the data protection authorities are available here: https://www.edoeb.admin.ch/edoeb/en/home.html. If you have questions or comments about your privacy rights, you may email us at admin@deenify.io. If you would at any time like to review or change the information in your account or terminate your account, you can: Log in to your account and make the changes that you want Upon your request to terminate your account, we will deactivate or delete your account and information from our active databases. However, we may retain some information in our files to prevent fraud, troubleshoot problems, assist with any investigations, enforce our Terms of Use and/or comply with applicable legal requirements.';
//    ' Opting out'
//    ' of email marketing: You can unsubscribe from our marketing email list at any time by clicking on the unsubscribe link'
//    ' in the emails that we send or by contacting us using the details provided below. You will then be removed from the ma'
//    'rketing email list – however, we may still communicate with you, for example to send you service-related emails that a'
//    're necessary for the administration and use of your account, to respond to service requests, or for other non-marketing'
//    ' purposes.'

let doNotTrack =
	'Most web browsers and some mobile operating systems and mobile applications include a Do-Not-Track (“DNT”) feature or setting you can activate to signal your privacy preference not to have data about your online browsing activities monitored and collected. At this stage, no uniform technology standard for recognizing and implementing DNT signals has been finalized. As such, we do not currently respond to DNT browser signals or any other mechanism that automatically communicates your choice not to be tracked online. If a standard for online tracking is adopted that we must follow in the future, we will inform you about that practice in a revised version of this privacy notice.';

let caliRights =
	'California Civil Code Section 1798.83, also known as the “Shine The Light” law, permits our users who are California residents to request and obtain from us, once a year and free of charge, information about categories of personal information (if any) we disclosed to third parties for direct marketing purposes and the names and addresses of all third parties with which we shared personal information in the immediately preceding calendar year. If you are a Californian resident and would like to make such a request, please submit your request in writing to us using our email admin@deenify.io. If you are under 18 years of age, reside in California, and have a registered account with the App, you have the right to request removal of unwanted data that you publicly post on the App. To request removal of such data, please contact us using the contact information provided , and include the email address associated with your account and a statement that you reside in California. We will make sure the data is not publicly displayed on the App, but please be aware that the data may not be completely or comprehensively removed from all our systems (e. g. backups, etc.).';

let noticeUpdate =
	'We may update this privacy notice from time to time. The updated version will be indicated by an updated “Revised” date and the updated version will be effective as soon as it is accessible. If we make material changes to this privacy notice, we may notify you either by prominently posting a notice of such changes or by directly sending you a notification. We encourage you to review this privacy notice frequently to be informed of how we are protecting your information.';

let updateData =
	'To request to review, update, or delete your personal information, edit your profile, change your password, delete your account or please send us an email at admin@deenify.com. We will respond to your request within 30 days.';
export default PrivacyPolicy;
